import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import {
  ContactList,
  CoverPage,
  DelayService,
  EnumDataSource,
  Feature,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataType,
  PasswordPolicy,
  Tab,
} from 'shared-code-lib';
import { CostCenter, FaxUser, FaxUserLoginMethod, FaxUserToAdd, Group } from '../../../blocks/InterFAX/Models/admin';
import { SupportedValues } from '../../../blocks/InterFAX/Models/outbound';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { ApiResults, Control } from '../../../blocks/services/models';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';

@Component({
  selector: 'app-fax-user-properties',
  templateUrl: './fax-user-properties.component.html',
  styleUrls: ['./fax-user-properties.component.scss'],
})
export class FaxUserPropertiesComponent implements OnInit, OnDestroy {
  @ViewChild('passwordInfoIcon', { static: false }) passwordInfoIconRef: ElementRef<HTMLElement>;
  @ViewChild('passwordPolicyInfo', { static: false }) passwordPolicyInfoRef: ElementRef<HTMLElement>;
  activeTab = 'groups';
  tabs = new Array<Tab>();

  formControls: Array<Control> = [];
  saveButton: Control;
  cancelButton: Control;

  show: boolean;

  routeParams: Params;

  fieldUsername: FieldDefinition;
  fieldEmail: FieldDefinition;
  fieldName: FieldDefinition;
  fieldUserType: FieldDefinition;
  fieldLoginMethod: FieldDefinition;
  fieldTimeZone: FieldDefinition;
  fieldCostCenter: FieldDefinition;
  fieldNotes: FieldDefinition;
  fieldCSID: FieldDefinition;
  fieldPassword: FieldDefinition;

  user: FaxUser | FaxUserToAdd;

  availableGroups: Array<Group> = [];
  currentGroups: Array<Group> = [];
  availableCoverPages: Array<CoverPage> = [];
  currentCoverPages: Array<CoverPage> = [];

  supportedValues: SupportedValues;

  contactLists: Array<ContactList>;

  title = '';

  showCostCenter = false;

  isCreating = false;

  passwordPolicy: PasswordPolicy;
  passwordPolicyInfoPosition = { top: '-100%', left: '-100%' };
  showPasswordPolicy = false;

  get userCostCenterId() {
    return `${this.user.costCenterId}`;
  }
  set userCostCenterId(val: string) {
    this.user.costCenterId = Number(val);
  }

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private interfaxService: InterFAXService,
    private toastrService: ToastrService,
    private errorsService: ErrorsService,
    private spinnerService: SpinnerStateService,
    private userState: UserStateService,
    private hydraApi: HydraApiService,
    private delayService: DelayService
  ) {}

  ngOnInit(): void {
    this.createButtons();

    this.showCostCenter = this.userState.hasFeature(Feature.InterFAX_CostCenters);

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.isCreating = data.type === 'create';
        this.load();
      })
    );
  }

  onSave = () => {
    this.spinnerService.showSavingSpinner();

    if (this.isCreating) {
      this.interfaxService.createFaxUser(this.user as FaxUserToAdd, this.saved, this.saveFailed);
    } else {
      this.interfaxService.setFaxUser(this.user, this.saved, this.saveFailed);
    }
  };

  saved = (results: ApiResults) => {
    this.spinnerService.hide();

    if (results.success) {
      this.toastrService.success(this.translateService.instant('fax-user-properties.save-success'));
      this.close();
    } else {
      this.errorsService.showErrorsModal(results.errors, 'fax-user-properties.save-fail');
    }
  };

  saveFailed = (): void => {
    this.spinnerService.hide();
    this.toastrService.error(this.translateService.instant('fax-user-properties.save-fail'));
  };

  onCancel = () => {
    this.close();
  };

  close = () => {
    this.router.navigateByUrl(
      'product/settings/InterFAX/InterFAX-Upland.Bamba.InterFAX.Administration.FaxUsers.Folder'
    );
  };

  currentGroupsChanged = (current: Array<Group>): void => {
    this.user.groups = current.map((c) => c.id);
    this.currentGroups = current;
  };

  currentCoverPagesChanged = (current: Array<CoverPage>): void => {
    this.user.coverPages = current.map((c) => c.id);
    this.currentCoverPages = current;
  };

  private load = () => {
    this.spinnerService.show(this.translateService.instant('Common.LoadingData'), document.documentElement);

    if (this.isCreating) {
      this.title = this.translateService.instant('fax-user-properties.header-text-add-new');

      this.hydraApi.getPasswordPolicy(this.passwordPolicyLoaded, this.passwordPolicyLoadFailed);
    } else {
      this.title = this.translateService.instant('fax-user-properties.header-text');
      this.interfaxService.getFaxUser(this.routeParams.username, this.userLoaded, this.loadFailed);
    }
  };

  passwordPolicyLoaded = (pp: PasswordPolicy): void => {
    this.passwordPolicy = pp;
    this.userLoaded(new FaxUserToAdd());
  };

  passwordPolicyLoadFailed = () => {
    this.toastrService.error(this.translateService.instant('MainHeader.failed-to-load-password-requirements'));
    this.spinnerService.hide();
  };

  private groupsLoaded = (groups: Array<Group>): void => {
    this.availableGroups = groups.map((g) => {
      g['icon'] = 'fa-users';
      return g;
    });

    this.interfaxService.getOutboundSupportedValues(this.supportedValuesLoaded, this.loadFailed);
  };

  private coverPagesLoaded = (coverPages: Array<CoverPage>): void => {
    this.availableCoverPages = coverPages.map((a: any) => {
      a.icon = 'fa-file-contract fa-fw fa-lg fas';
      return a;
    });

    this.finishLoading();
  };

  supportedValuesLoaded = (sv: SupportedValues): void => {
    this.supportedValues = sv;

    this.interfaxService.getAccountCoverpages(this.coverPagesLoaded, this.loadFailed);
  };

  private userLoaded = (faxUser: FaxUser) => {
    this.user = faxUser;

    this.interfaxService.getGroups(this.groupsLoaded, this.loadFailed);
  };

  finishLoading = (): void => {
    this.createFields();

    this.currentGroups = this.availableGroups.filter((g) => this.user.groups.findIndex((a) => a == g.id) != -1);
    this.currentCoverPages = this.availableCoverPages
      .filter((c) => this.user.coverPages.findIndex((a) => a == c.id) != -1)
      .sort((x, y) => x.name.localeCompare(y.name));

    this.spinnerService.hide();
    this.createTabs();
    this.show = true;
  };

  loadFailed = (err: any) => {
    //todo: should show error probably
    this.spinnerService.hide();
  };

  createTabs = (): void => {
    this.tabs = [];
    this.tabs.push(new Tab('groups', this.translateService.instant('fax-user-properties.groups')));
    this.tabs.push(new Tab('cover-pages', this.translateService.instant('fax-user-properties.cover-pages')));
  };

  private createFields = () => {
    this.fieldUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.username'),
      placeholder: '',
      required: true,
    });

    this.fieldEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.email'),
      placeholder: '',
      required: false,
    });

    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.name'),
      placeholder: '',
      required: false,
    });

    this.fieldUserType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('fax-user-properties.usertype'),
      placeholder: '',
      required: false,
      datasource: this.getUserTypesDataSource(),
    });

    this.fieldLoginMethod = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('fax-user-properties.login-method'),
      placeholder: '',
      required: false,
      datasource: new EnumDataSource(FaxUserLoginMethod, 'FaxUserLoginMethodEnum', this.translateService),
    });

    this.fieldTimeZone = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('fax-user-properties.timezone'),
      placeholder: '',
      required: true,
      datasource: this.getTimeZoneDataSource(),
    });

    this.fieldCostCenter = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('fax-user-properties.cost-center'),
      placeholder: '',
      required: false,
      datasource: new CostCenterDataSource(this.interfaxService, this.translateService),
    });

    this.fieldNotes = new FieldDefinition({
      type: MetaDataType.LargeText,
      label: this.translateService.instant('fax-user-properties.notes'),
      placeholder: '',
      required: false,
    });

    this.fieldCSID = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.csid'),
      placeholder: '',
      required: false,
    });

    if (!this.isCreating) return;

    this.fieldPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translateService.instant('fax-user-properties.password'),
      placeholder: '',
      required: true,
    });
  };

  getTimeZoneDataSource = (): FixedDataSource => {
    const options = new Array<IFieldOptions>();

    if (!this.supportedValues) return;
    for (const tz of this.supportedValues.timeZones) {
      options.push({ name: tz, value: tz });
    }

    return new FixedDataSource(options);
  };

  getUserTypesDataSource = (): FixedDataSource => {
    const options = new Array<IFieldOptions>();

    options.push({ name: this.translateService.instant('fax-user-properties.usertype-regular'), value: 'Normal' });
    options.push({
      name: this.translateService.instant('fax-user-properties.usertype-account-manager'),
      value: 'Primary',
    });

    return new FixedDataSource(options);
  };

  activateTab = (tab: string): void => {
    this.activeTab = tab;
  };

  private createButtons() {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.formControls.push(this.saveButton);
    this.formControls.push(this.cancelButton);

    this.translateService.get('Metadata.Save').subscribe((res) => {
      this.saveButton.setTranslation(res);
    });
    this.translateService.get('Metadata.Cancel').subscribe((res) => {
      this.cancelButton.setTranslation(res);
    });
  }

  showPasswordPolicyInfo() {
    this.showPasswordPolicy = true;
    this.delayService.execute(this.setPasswordPolicyInfoPosition, 50);
  }

  setPasswordPolicyInfoPosition = () => {
    let { top, left } = this.passwordInfoIconRef.nativeElement.getBoundingClientRect();
    const passwordPolicyInfoHeight = this.passwordPolicyInfoRef.nativeElement.clientHeight;

    const maxTop = window.innerHeight - 10;

    if (top + passwordPolicyInfoHeight > maxTop) top = maxTop - passwordPolicyInfoHeight;

    this.passwordPolicyInfoPosition = { top: top + 'px', left: left + 20 + 'px' };
  };

  hidePasswordPolicyInfo() {
    this.showPasswordPolicy = false;
    this.passwordPolicyInfoPosition = { top: '-100%', left: '-100%' };
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}

class CostCenterDataSource extends FixedDataSource {
  constructor(private interfaxService: InterFAXService, private translateService: TranslateService) {
    super();
  }

  successCallBack: (items: IFieldOptions[]) => void;
  failureCallBack: (err: any) => void;

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    this.successCallBack = successCallBack;
    this.failureCallBack = failureCallBack;

    this.interfaxService.getCostCenters(this.loaded, this.failed);
  }

  loaded = (costCenters: Array<CostCenter>): void => {
    const options = new Array<IFieldOptions>();

    options.push({ name: this.translateService.instant('fax-user-properties.no-cost-center'), value: '0' });

    costCenters.forEach((p) => options.push({ name: p.name, value: p.id }));

    options.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));

    this.successCallBack(options);
  };

  failed = (err): void => {
    this.failureCallBack(err);
  };
}
