import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FaxUserPropertiesComponent } from './fax-user-properties.component';

import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalModule } from 'ngx-smart-modal';
import {
  AutoFocusFirstInputModule,
  AutoUpdatePerfectScrollModule,
  CheckboxModule,
  FieldsModule,
  IconsModule,
  LabelModule,
  ModalHeaderModule,
  TabBarModule,
} from 'shared-code-lib';
import { PasswordPolicyModule } from '../../../../../../../Bamba.Shared/src/auth/password-policy/password-policy.module';
import { ComponentsModule } from '../../../blocks/components/components.module';
import { PageModule } from '../../../blocks/page/page.module';
import { AdminInboundFaxSettingsModule } from '../admin-inbound-fax-settings/admin-inbound-fax-settings.module';
import { AdminOutboundFaxSettingsModule } from '../admin-outbound-fax-settings/admin-outbound-fax-settings.module';

const routes: Routes = [
  {
    path: 'create',
    component: FaxUserPropertiesComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },

  {
    path: ':username',
    component: FaxUserPropertiesComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminFaxUserPropertiesRoutingModule {}

@NgModule({
  declarations: [FaxUserPropertiesComponent],
  imports: [
    CommonModule,
    TranslateModule,
    PageModule,
    ComponentsModule,
    FormsModule,
    PerfectScrollbarModule,
    IconsModule,
    FieldsModule,
    AutoFocusFirstInputModule,
    TabBarModule,
    CheckboxModule,
    NgxSmartModalModule,
    ModalHeaderModule,
    LabelModule,
    AdminFaxUserPropertiesRoutingModule,
    AdminInboundFaxSettingsModule,
    AdminOutboundFaxSettingsModule,
    AutoUpdatePerfectScrollModule,
    PasswordPolicyModule,
  ],
})
export class AdminFaxUserPropertiesModule {}
